.swiper__swiper {
  max-width: 64rem;
}

.swiper__swiper-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.46rem;
  margin-top: 3.04rem;
}

.swiper-button-prev,
.swiper-button-next {
  z-index: 99;
  width: 4.1rem;
  height: 4.1rem;
  border: 1px solid #2B2B51;
}

.swiper-button-prev {
  content: "";
  display: block;
  width: 2.9rem;
  height: 2.9rem;
  left: 0;
  font-size: 1rem;
  background-color: #ffffff;
  border: 1px solid #2B2B51;
  border-radius: 50%;
  opacity: 1;
}

@media only screen and (min-width: 992px) {
  .swiper-button-prev {
    width: 4.1rem;
    height: 4.1rem;
  }
}

.swiper-button-prev:after {
  position: absolute;
  font-size: 1.2rem;
  font-weight: 700;
  color: #2B2B51;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -50%);
}

.swiper-button-next {
  content: "";
  display: block;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  right: 0;
  background-color: #ffffff;
  border: 1px solid #2B2B51;
  border-radius: 50%;
  opacity: 1;
}

@media only screen and (min-width: 992px) {
  .swiper-button-next {
    width: 4.1rem;
    height: 4.1rem;
  }
}

.swiper-button-next:after {
  position: absolute;
  font-size: 1.2rem;
  font-weight: 700;
  color: #2B2B51;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -50%);
}

.swiper-nav > div {
  cursor: pointer;
}

.swiper-nav > div:before {
  transition: all 3000ms ease-in-out;
}

.swiper-active__slide {
  position: relative;
}

.swiper-active__slide:before {
  position: absolute;
  content: "";
  inset: 0;
  background-color: var(--color-light-grey);
  opacity: 0.7;
}

.slider-nav .swiper-slide {
  margin-right: 0 !important;
  min-width: 8.011rem;
}

@media only screen and (min-width: 768px) {
  .slider-nav .swiper-slide {
    margin-right: 0 !important;
    margin-right: 0 !important;
  }
}

@media only screen and (min-width: 1280px) {
  .slider-nav .swiper-slide {
    width: fit-content !important;
  }
}

.slider-nav > .swiper-slide-thumb-active {
  position: relative;
}

.slider-nav > .swiper-slide-thumb-active::after {
  position: absolute;
  content: "";
  inset: 0;
  background-color: #f8f8f8;
  opacity: 0.7;
  z-index: 99;
}

@media only screen and (min-width: 768px) {
  .slider-single > .swiper-slide {
    width: 100% !important;
  }
}

