.row {
  display: flex;

  &.-jc {
    &_sb {
      justify-content: space-between;
    }
  }

  &.-ai {
    &_ctr {
      align-items: center;
    }
  }

  &.-spcol {
    @include mq("tablet", "max") {
      flex-direction: column;
    }

    &-rev {
      @include mq("tablet", "max") {
        flex-direction: column-reverse;
      }
    }
  }

  &._gap {
    &-110 {
      gap: 30px;
      @include mq("tablet") {
        gap: 60px;
      }
      @include mq("tablet-wide") {
        gap: 110px;
      }
    }
    &-130 {
      gap: 130px;
    }
    &-20 {
      gap: 20px;
    }
    &-68 {
      gap: 68px;
    }
    &-80 {
      gap: 30px;
      @include mq("tablet") {
        gap: 80px;
      }
    }
  }
}

.col {
  &__half {
    flex-basis: 50%;
    max-width: 50%;
  }

  &._grow {
    flex-grow: 1;
  }
  &._shrink {
    flex-shrink: 1;
  }

  &._is {
    &-45 {
      flex-basis: 44%;
      max-width: 44%;
    }
    &-55 {
      flex-basis: 56%;
      max-width: 56%;
    }
  }
}
