*,
*::after,
*::before {
  box-sizing: border-box;
}
html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  font-family: $font-primary;
  font-size: 1.6rem;
  font-weight: 400;
  // color: $c-black;
  // line-height: 1.2;
  // background: $c-white;

  &.hide-overflow {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

.container {
  display: block;
  max-width: 100%;
  overflow: hidden;
  padding: 0 10px;
  margin: 0 auto;

  @include mq("phablet") {
    padding: 0;
    max-width: 540px;
  }

  @include mq("tablet") {
    max-width: 700px;
  }

  @include mq("tablet-wide") {
    max-width: 900px;
  }

  @include mq("desktop") {
    max-width: 1280px;
    width: 85vw;

    &.-info {
      max-width: 1301px;
    }
  }

  &-lg {
    padding: 0 15px;
    @include mq("tablet") {
      max-width: calc(100% - 30px);
      margin: 0 auto;
      padding: 0;
    }
    @include mq("tablet-wide") {
      max-width: calc(100% - 70px);
    }
    @include mq("desktop") {
      width: 92.7vw;
      max-width: 1780px;
    }
  }

  &.-sm {
    max-width: 1000px;
  }
}

%reset-ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}
