.cc-nav {
  &-list {
    &__item {
      &:not(:last-child) {
        padding-right: 70px;
        @include mq("tablet") {
          padding-right: 40px;
        }
        @include mq("tablet-wide") {
          padding-right: 70px;
        }
      }
      &-link {
        &:visited,
        &:link {
          font-size: 1.5rem;
          color: $c-text;
          font-weight: 600;
          transition: 0.4s all ease-in-out;

          @include mq("tablet") {
            font-size: 1.4rem;
          }
          @include mq("tablet-wide") {
            font-size: 1.8rem;
          }
        }

        &:hover {
          opacity: 0.5;
          transition: 0.4s all ease-in-out;
        }
      }
    }
  }
}
