.cc-header {
  position: relative;
  padding: 16px 0;
  z-index: 5;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
  
  @include mq("tablet") {
    border-bottom: none;
    padding: 20px 0;
    margin-bottom: 0;
    box-shadow: none;
  }
  @include mq("tablet-wide") {
    padding: 30px 0;
  }

  &__img {
    position: relative;
    z-index: 5;
    img {
      width: 120px;
      @include mq("phablet") {
        width: 140px;
      }
      @include mq("tablet") {
        width: 160px;
      }
      @include mq("tablet-wide") {
        width: 180px;
      }
      @include mq("desktop") {
        width: 200px;
      }
    }
  }

  .ham-menu {
    position: relative;
    width: 18px;
    height: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;

    &:before,
    &:after {
      content: "";
      height: 1px;
      position: absolute;
      width: 18px;
      left: 50%;
      transform: translateX(-50%);
      z-index: 1;
      background-color: $c-white;
      transition: 0.4s all ease-in-out;
      border-radius: 10px;
    }

    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }

    > span {
      display: block;
      width: 100%;
      max-width: 18px;
      height: 1px;
      background-color: $c-white;
      transition: 0.4s all ease-in-out;

      &:before,
      &:after {
        content: "";
        height: 1px;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 1;
        max-width: 18px;
        background-color: transparent;
        top: 50%;
        border-radius: 10px;
        transition: 0.4s all 0.1s ease-in-out;
      }
    }

    &.is-opened {
      transition: 0.4s all ease-in-out;
      &:before,
      &:after {
        height: 0px;
        transition: 0.4s all ease-in-out;
      }
      > span {
        background-color:transparent;
        transition: 0.4s all ease-in-out;
        &:before,
        &:after {
          transform-origin: center;
          transition: 0.4s all 0.1s ease-in-out;
          background-color: transparent;
        }
        &:before {
          transform: rotate(45deg);
          background-color:$c-white;
        }
        &:after {
          transform: rotate(-45deg);
          background-color:$c-white;
        }
      }

      ~ .cc-nav {
        opacity: 1;
        visibility: visible;
        transition: 0.4s all ease-in-out;
        transform: scaleY(1);
      }
    }

    @include mq("tablet") {
      display: none;
    }
  }

  @include mq("tablet", "max") {
    .cc-nav {
      position: absolute;
      // inset: 0;
      left: 0;
      right: 0;
      top: calc(100% + 1px);
      background-color: $c-white;
      display: flex;
      z-index: 4;
      opacity: 0;
      visibility: hidden;
      transition: 0.4s all ease-in-out;
      padding: 20px 0px;
      transform: scaleY(0);
      transform-origin: top;

      &-list {
        flex-direction: column;
        gap: 20px;
        flex-grow: 1;

        &__item {
          font-size: 15px;
          font-weight: 600;
          padding-inline: 15px;
          padding-bottom: 18.5px;
          border-bottom: 2px solid $c-gray;
          position: relative;

          &:before{
            position: absolute;
            content: '';
            right: 15px;
            width: 20px;
            height: 20px;
            background-image: url("data:image/svg+xml,%3Csvg id='Group_91' data-name='Group 91' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='23.135' height='23.179' viewBox='0 0 23.135 23.179'%3E%3Cdefs%3E%3CclipPath id='clip-path'%3E%3Crect id='Rectangle_19' data-name='Rectangle 19' width='23.135' height='23.179' fill='%2b2bf1'/%3E%3C/clipPath%3E%3C/defs%3E%3Cg id='Group_44' data-name='Group 44' clip-path='url(%23clip-path)'%3E%3Cpath id='Path_16' data-name='Path 16' d='M10.845,0h1.446c.148.019.3.039.444.055A11.339,11.339,0,0,1,18.852,2.6a11.408,11.408,0,0,1,4.106,6.993c.076.421.119.848.178,1.272v1.449c-.035.283-.066.567-.106.849a11.408,11.408,0,0,1-2.811,6.1A11.376,11.376,0,0,1,13.582,23c-.427.077-.86.121-1.291.18H10.845c-.267-.033-.536-.06-.8-.1A11.367,11.367,0,0,1,2.5,18.759,11.261,11.261,0,0,1,.108,10.046,11.228,11.228,0,0,1,3.379,3.42,11.3,11.3,0,0,1,9.6.175C10.011.1,10.429.058,10.845,0M21.689,11.584A10.121,10.121,0,1,0,11.562,21.731,10.147,10.147,0,0,0,21.689,11.584' transform='translate(-0.001)' fill='%2b2bf1'/%3E%3Cpath id='Path_17' data-name='Path 17' d='M115.391,125.048h-2.653c-1.408,0-2.815,0-4.223,0a.722.722,0,0,1-.709-1,.705.705,0,0,1,.7-.444h6.88c-.082-.09-.133-.15-.189-.205-.718-.72-1.44-1.437-2.154-2.162a.718.718,0,0,1,.3-1.226.67.67,0,0,1,.689.173q1.825,1.819,3.64,3.648a.692.692,0,0,1-.008,1q-1.806,1.822-3.625,3.632a.705.705,0,0,1-1.015,0,.716.716,0,0,1,.014-1.033c.715-.724,1.436-1.441,2.154-2.161.057-.057.111-.116.2-.213' transform='translate(-101.241 -112.734)' fill='%2b2bf1'/%3E%3C/g%3E%3C/svg%3E%0A");
            background-position: center;
            background-size: cover;
          }

          &-link{
            font-size: 1.5rem;

          }
        }
      }
    }
  }
}

.ham__wrapper{
  width: 41px;
  height: 41px;
  background-color: $c-primary;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;

  @include mq("tablet"){
    display: none;
  }
}

.aside {
  width: 0%;
  backdrop-filter: blur(5px);
  position: fixed;
  right: 0;
  top: 0;
  overflow-y: hidden;
  z-index: 99;
  visibility: visible;
  opacity: 0;
  transition: all 0.5s ease-in-out;

  &.is-clicked{
    width: 100%;
    opacity: 1;
  }

  @include mq("tablet"){
    visibility: hidden;
  }

  &__header{

    padding: 15px 18px;
    background-color: $c-primary;
    display: flex;
    justify-content: space-between;

    &>svg{
      width: 150px;
      height: 41px;
      fill: $c-white;
    }
  }
  &__wrapper{
    background-color: $c-white;
    width: 70%;
    height: 100vh;
    margin-left: auto;
  }
  &__navbar{
    background-color: $c-primary;
    &>.cc-nav{
      // display: none;
      position: static;
      opacity: 1;
      visibility: visible;
      transition: 0.4s all ease-in-out;
      transform: scaleY(1);

      @include mq("tablet"){
        visibility: hidden;
      }
    }
  }

  &__controller{
    width: 42px;
    height: 42px;
    border: 1px solid $c-white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    &>span{
      display: inline-block;
      position: relative;
      width: 12px;
      height: 12px;
      &:before,&:after{
        position: absolute;
        content: '';
        width: 18px;
        height: 1px;
        border-radius: 3px;
        background-color: $c-white;
      }

      &:before{

        transform: rotate(45deg) translate(0px , 5px);
      }
      &:after{
        transform: rotate(135deg) translate(5px , 0px);
      }
    }
  }
}

