.cc-hero {
  height: 45vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 50px;

  @include mq("tablet") {
    border-radius: 10px;
    margin-bottom: 100px;
    height: 60vh;
  }
  @include mq("tablet-wide") {
    margin-bottom: 150px;
    height: 75vh;
  }
  @include mq("desktop") {
    margin-top: 0;
    margin-bottom: 150px;
    height: 86.63vh;
  }

  &__text {
    padding-top: 80px;
    font-size: 2.2rem;
    color: $c-white;
    font-weight: 500;
    line-height: 1.45;
    margin-bottom: 35px;

    @include mq("tablet") {
      padding-top: 100px;
      font-size: 3rem;
    }
    @include mq("desktop") {
      padding-top: 160px;
      font-size: 4rem;
      line-height: 1.8;
    }
  }
}
