.vehicle-information {
  &__wrapper {
    margin: 4.5rem 0 10.3rem 0;
    position: relative;
    @include mq("tablet") {
      margin: 10.5rem 0 18.5rem 0;
    }
  }
  &__row {
    display: flex;
    flex-direction: column;
    @include mq("tablet") {
      flex-direction: column;
      justify-content: space-between;
    }
  }
  &__content {
    max-width: 100%;
    padding-inline: 0.5rem;
    order: 3;
    @include mq ("tablet"){
    max-width: 40%;
    }
    @include mq("tablet-wide"){
      max-width: 40%;
    }
    @include mq("desktop") {
      max-width: 40%;
      padding-inline: 0;
    }
    @include mq("desktop-lg")
  }
  &__heading {
    &--bg-dark {
      font-size: 2rem;
      font-weight: 700;
      line-height: calc(29 / 20);
      padding: 0.6rem 0 0.6rem 2rem;
      color: $c-white;
      background-color: $c-primary;
      border-radius: 0.4rem;

      @include mq("tablet-wide") {
        font-size: 2.5rem;
        padding: 1.2rem 0 1.2rem 2rem;
      }
    }
    &-tailed {
      max-width: 100%;
      width: 100%;
      color: $c-primary;
      font-size: 1.6rem;
      font-weight: 700;
      line-height: 2.25;
      letter-spacing: 0.05em;
      margin-block: 2.5rem 1.5rem;
      position: relative;
      @include mq("tablet-wide") {
        font-size: 2rem;
        margin-block: 5rem 2.25rem;
      }
      @include mq("desktop") {
        max-width: 52.6rem;
      }
    }
    &-tailed:after {
      position: absolute;
      content: "";
      bottom: 0;
      left: 0;
      width: 100%;
      height: 0.2rem;
      border-radius: 0.5rem;
      background-color: $c-gray;
    }
    &-tailed > span {
      position: relative;
      display: inline-block;
    }
    &-tailed > span:before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 0;
      width: 110%;
      height: 0.2rem;
      border-radius: 0.5rem;
      background-color: $c-primary;
      z-index: 2;
    }
  }
  &__nb-simple {
    border: none;
    & > tbody > tr > th {
      text-align: left;
      font-weight: inherit;
    }
    & > tbody > tr {
      background-color: $c-white !important;
    }
  }
  &__button,
  &__button:link,
  &__button:visited,
  &__button:active,
  &__button:focus {
    text-decoration: underline;
    margin-top: 1.5rem;
    transition: 0.3s ease-in-out;
    display: inline-block;
    color: $c-black;
    @include mq("tablet-wide") {
      margin-top: 3rem;
    }
    &:hover {
      opacity: 0.5;
    }
    &__usage-fee{
      padding-inline: 0.5rem;
    }
  }
  &__swiper {
    max-width: 100%;
    margin-block: 2.4rem 1.6rem;
    order: 2;
    position: static;
    @include mq("tablet") {
      max-width: 55%;
      margin-block: 0;
      position: absolute;
      right: 0;
    }
    @include mq("desktop") {
      max-width: 68rem;
    }
  }
  &__slide {
    &-small-img {
      max-width: 100%;
      width: 100%;
      height: auto;
      border-radius: 0.4rem;

      @include mq("tablet") {

        height: auto;
      }
      @include mq("tablet-wide"){
        max-width: 14.862rem;
        height: auto;
      }
      @include mq ("desktop"){
        height: 10.68rem;
      }
    }
    &-big-img {
      max-width: 100%;
      width: 100%;
      height: auto;
      border-radius: 0.6rem;
      object-fit: cover;
      object-position: center;
      @include mq("desktop ") {
        max-width: 64rem;
        height: 46rem;
      }
    }
  }
  &__table {
    &-slider {
      width: 100%;
      overflow-x: auto;
    }
    &-primary {
      color: $c-white;
      background-color: $c-white-gray;
      border-collapse: separate;
      border-spacing: 1px;
      min-width: 45rem;
    }
    &-primary > * > * > th.bg-dark {
      background-color: $c-primary;
    }
    &-primary > * > * > th.bg-light {
      background-color: $c-navy;
    }
    &-primary > tbody > tr > th {
      text-align: left;
      padding-inline: 1.15rem 0.5rem;
      @include mq("tablet-wide") {
        padding-left: 2.3rem;
      }
    }
    &-primary > tbody > tr > td {
      text-align: center;
      color: $c-black;
      background-color: $c-white-gray;
      border-collapse: collapse;
    }
  }
  &__swiper {
    &-container {
      display: flex;
      justify-content: space-between;
      row-gap: 1.46rem;
      column-gap: 0.85  rem;
      margin-top: 1.4rem;
      flex-wrap: nowrap;
      transform: none !important;
      @include mq("tablet-wide") {
        column-gap: 1.7rem;
        justify-content: unset;
      }
      @include mq ("desktop"){
        column-gap: 1.27rem;
      }
    }
  }
  &__detail{
    &-wrapper{
      width: 100%;
      @include mq ("tablet"){
        width: 40%;
      }
    }
  }
}

.detail {
  &-wrapper{
    width: 100%;
    @include mq ("tablet"){
      width: 40%;
    }
  }
  &__fwm {
    &--fs18_14 {
      font-size: 1.4rem;
      font-weight: 500;
      line-height: 1.6666666667;
      margin-block: 1.7rem 2.4rem;
      @include mq("tablet-wide") {
        font-size: 1.8rem;
        margin-block: 2.5rem 4rem;
      }
    }
    &--fs16_14 {
      font-size: 1.4rem;
      font-weight: 500;
      line-height: 1.875;

      @include mq("tablet-wide") {
        font-size: 1.6rem;
      }
    }
  }
  &__news--fwmd__fs14_8 > li {
    font-size: 1.4rem;
    font-weight: 500;
    color: $c-news-lgray;
    line-height: calc(25 / 14);

    &:before {
      content: "※";
      display: inline-block;
    }
  }
  &__fwn {
    font-weight: 400;
    &--fs18_12 {
      font-size: 1.2rem;
      font-weight: 400;
      line-height: 2.5;

      @include mq("tablet") {
        font-size: 1.8rem;
      }
    }
    &--fs16_10 {
      font-size: 1rem;
      font-weight: 400;
      line-height: 2.8125;
      @include mq("tablet") {
        font-size: 1.6rem;
      }
    }
  }
}

.swiper {
  &__swiper {
    max-width: 64rem;

    &-container {
      display: flex;
      flex-wrap: wrap;
      gap: 1.46rem;
      margin-top: 3.04rem;
    }
  }

  &-button-prev,
  &-button-next {
    z-index: 99;
    width: 4.1rem;
    height: 4.1rem;
    border: 1px solid $c-primary;
  }

  &-button {
    &-prev {
      content: "";
      display: block;
      width: 2.9rem;
      height: 2.9rem;
      left: 0;
      font-size: 1rem;
      background-color: $c-white;
      border: 1px solid $c-primary;
      border-radius: 50%;
      opacity: 1;
      @include mq("tablet-wide") {
        width: 4.1rem;
        height: 4.1rem;
      }
      &:after {
        position: absolute;
        font-size: 1.2rem;
        font-weight: 700;
        color: $c-primary;
        top: 50%;
        left: 50%;
        transform: translate(-70%, -50%);
      }
    }
    &-next {
      content: "";
      display: block;
      font-size: 2rem;
      width: 2.5rem;
      height: 2.5rem;
      right: 0;
      background-color: $c-white;
      border: 1px solid $c-primary;
      border-radius: 50%;
      opacity: 1;

      @include mq("tablet-wide") {
        width: 4.1rem;
        height: 4.1rem;
      }
      &:after {
        position: absolute;
        font-size: 1.2rem;
        font-weight: 700;
        color: $c-primary;
        top: 50%;
        left: 50%;
        transform: translate(-30%, -50%);
      }
    }
  }

  &-nav > div {
    cursor: pointer;
    &:before {
      transition: all 3000ms ease-in-out;
    }
  }
  &-active__slide {
    position: relative;

    &:before {
      position: absolute;
      content: "";
      inset: 0;
      background-color: var(--color-light-grey);
      opacity: 0.7;
    }
  }
}

.slider {
  &-nav {
    & .swiper-slide {
      margin-right: 0 !important;
      min-width: 8.011rem;
      
      @include mq("tablet") {
      margin-right: 0 !important;
      margin-right: 0 !important;
      }

      @include mq("tablet-wide") {
      }
      @include mq("desktop"){

        width: fit-content !important;
      }
    }

    & > .swiper-slide-thumb-active {
      position: relative;

      &::after {
        position: absolute;
        content: "";
        inset: 0;
        background-color: $c-white-gray;
        opacity: 0.7;
        z-index: 99;
      }
    }
  }
  &-single {
    & > .swiper-slide {
      @include mq("tablet") {
        width: 100% !important;
      }
    }
  }
}
.cc-swiper {
  &__main {
    padding-inline: 0.5rem;
    @include mq("desktop") {
      padding-inline: 2rem;
    }
  }
  &__thumb {
    margin-inline: 0;
    padding-inline: 0.5rem;
    @include mq("desktop") {
      margin-inline: 2rem;
    }
  }
}
