.cc {
  &-heading {
    &__primary {
      font-size: 1.8rem;
      color: $c-primary;
      text-align: center;
      font-weight: 700;

      @include mq("phablet") {
        font-size: 2.2rem;
      }
      @include mq("tablet") {
        font-size: 2.4rem;
      }
      @include mq("tablet-wide") {
        font-size: 2.6rem;
      }
      @include mq("desktop") {
        font-size: 2.8rem;
      }

      &:before {
        content: attr(data-title);
        display: block;
        font-size: 1.2rem;
        letter-spacing: 0.05em;
        font-weight: 500;
        color: $c-primary;
        margin-bottom: 10px;

        @include mq("tablet") {
          margin-bottom: 15px;
        }
        @include mq("desktop") {
          margin-bottom: 20px;
        }
      }

      &.-white {
        color: $c-white;
        &:before {
          color: $c-white;
        }
      }
    }
    &__secondary {
      font-size: 2rem;
      line-height: 1.42;
      color: $c-primary;
      font-weight: 700;

      @include mq("phablet") {
        font-size: 2.2rem;
      }
      @include mq("tablet") {
        font-size: 2.4rem;
      }
      @include mq("tablet-wide") {
        font-size: 2.6rem;
      }
      @include mq("desktop") {
        font-size: 2.8rem;
        line-height: 1.8;
      }

      &.-center {
        text-align: center;
      }
    }

    &__small {
      background-color: $c-primary;
      padding: 2px 15px;
      display: inline-block;
      border-radius: 4px;
      font-size: 1.2rem;
      color: $c-white;
      line-height: 1.35;
      font-weight: 500;
      margin-bottom: 5px;

      @include mq("tablet") {
        font-size: 1.4rem;
      }
      @include mq("desktop") {
        font-size: 1.6rem;
      }
    }
  }

  &-paragraph {
    &__primary,
    &__primary a {
      font-size: 1.2rem;
      line-height: 2.5;
      color: $c-text;
      letter-spacing: 0.05em;

      @include mq("tablet") {
        font-size: 1.4rem;
      }
      @include mq("tablet-wide") {
        font-size: 1.6rem;
      }
      @include mq("desktop") {
        font-size: 1.8rem;
      }

      &.-center {
        text-align: center;
      }
    }
  }

  &-button {
    &__box {
      text-align: center;
    }
    &__primary {
      border-radius: 28px;
      border: 1px solid $c-primary;
      transition: 0.4s all ease-in-out;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $c-primary;
      width: 190px;
      padding: 10px 20px;

      @include mq("phablet") {
        display: inline-flex;
        width: 200px;
        margin: 0;
        padding: 15px 25px;
      }
      @include mq("tablet") {
        width: 210px;
      }
      @include mq("tablet-wide") {
        width: 220px;
      }
      @include mq("desktop") {
        width: 230px;
      }

      span,
      &:link span,
      &:visited span,
      &:focus span {
        color: $c-white;
        font-size: 1.4rem;
        font-weight: 500;
        flex: 1 0 calc(100% - 23px);
        text-align: center;
        letter-spacing: 0.05;
        line-height: 1.3;
        transition: 0.4s all ease-in-out;

        @include mq("tablet") {
          font-size: 1.4rem;
        }
        @include mq("tablet-wide") {
          font-size: 1.6rem;
        }
        @include mq("desktop") {
          font-size: 1.8rem;
        }
      }

      &:hover {
        background-color: $c-white;
        span {
          color: $c-primary;
          transition: 0.4s all ease-in-out;
        }
      }

      &.-white,
      &.-white:link,
      &.-white:visited,
      &.-white:focus {
        background-color: $c-white;
        span {
          color: $c-primary;
        }

        &:hover {
          background-color: $c-primary;
          span {
            color: $c-white;
            transition: 0.4s all ease-in-out;
          }
        }
      }

      &.-sidearrow {
        &:after {
