          background-image: url("data:image/svg+xml,%3Csvg id='Group_91' data-name='Group 91' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='23.135' height='23.179' viewBox='0 0 23.135 23.179'%3E%3Cdefs%3E%3CclipPath id='clip-path'%3E%3Crect id='Rectangle_19' data-name='Rectangle 19' width='23.135' height='23.179' fill='%23fff'/%3E%3C/clipPath%3E%3C/defs%3E%3Cg id='Group_44' data-name='Group 44' clip-path='url(%23clip-path)'%3E%3Cpath id='Path_16' data-name='Path 16' d='M10.845,0h1.446c.148.019.3.039.444.055A11.339,11.339,0,0,1,18.852,2.6a11.408,11.408,0,0,1,4.106,6.993c.076.421.119.848.178,1.272v1.449c-.035.283-.066.567-.106.849a11.408,11.408,0,0,1-2.811,6.1A11.376,11.376,0,0,1,13.582,23c-.427.077-.86.121-1.291.18H10.845c-.267-.033-.536-.06-.8-.1A11.367,11.367,0,0,1,2.5,18.759,11.261,11.261,0,0,1,.108,10.046,11.228,11.228,0,0,1,3.379,3.42,11.3,11.3,0,0,1,9.6.175C10.011.1,10.429.058,10.845,0M21.689,11.584A10.121,10.121,0,1,0,11.562,21.731,10.147,10.147,0,0,0,21.689,11.584' transform='translate(-0.001)' fill='%23fff'/%3E%3Cpath id='Path_17' data-name='Path 17' d='M115.391,125.048h-2.653c-1.408,0-2.815,0-4.223,0a.722.722,0,0,1-.709-1,.705.705,0,0,1,.7-.444h6.88c-.082-.09-.133-.15-.189-.205-.718-.72-1.44-1.437-2.154-2.162a.718.718,0,0,1,.3-1.226.67.67,0,0,1,.689.173q1.825,1.819,3.64,3.648a.692.692,0,0,1-.008,1q-1.806,1.822-3.625,3.632a.705.705,0,0,1-1.015,0,.716.716,0,0,1,.014-1.033c.715-.724,1.436-1.441,2.154-2.161.057-.057.111-.116.2-.213' transform='translate(-101.241 -112.734)' fill='%23fff'/%3E%3C/g%3E%3C/svg%3E%0A");
          transition: 0.4s all ease-in-out;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100% 100%;

          @include mq("phablet") {
            flex-basis: 20px;
            width: 20px;
            height: 20px;
          }
          @include mq("tablet") {
            flex-basis: 21px;
            width: 21px;
            height: 21px;
          }
          @include mq("tablet-wide") {
            flex-basis: 22px;
            width: 22px;
            height: 22px;
          }
          @include mq("desktop") {
            flex-basis: 23px;
            width: 23px;
            height: 23px;
          }
        }

        &:hover {
          &:after {
