.cc {
  &-about {
    margin: 45px 0 60px;
    @include mq("tablet") {
      margin: 150px 0 90px;
    }
    &__heading {
      margin-bottom: 20px;
      @include mq("tablet") {
        margin-bottom: 45px;
      }
    }

    .cc-button__primary{
      display: none;
      @include mq ("tablet"){
        display: flex;
      }
    }

    .row._gap-110{
      gap:0 ;
      @include mq("tablet"){

      }
    }

    &__paragraph {
      margin-bottom: 20px;
      @include mq("phablet") {
        margin-bottom: 40px;
      }
      @include mq("tablet") {
        margin-bottom: 50px;
      }
      @include mq("tablet-wide") {
        margin-bottom: 60px;
      }
      @include mq("desktop") {
        margin-bottom: 70px;
      }
    }

    .col._grow {
      @include mq("tablet") {
        flex-basis: 40%;
        max-width: 40%;
      }
      @include mq("desktop") {
        flex-basis: 46%;
        max-width: 46%;
        flex: 1 0 46%;
      }
    }
    .col._shrink {
      @include mq("tablet") {
        flex-basis: 60%;
        max-width: 60%;
      }
      @include mq("desktop") {
        flex-basis: 560px;
        max-width: 560px;
        flex: 1 0 560px;
      }
    }
  }

  &-vehicles {
    background-color: $c-bg;
    padding: 40px 0 40px;

    &-heading {
      margin-bottom: 30px;
      @include mq("phablet") {
        margin-bottom: 30px;
      }
      @include mq("tablet") {
        margin-bottom: 40px;
      }
      @include mq("tablet-wide") {
        margin-bottom: 50px;
      }
      @include mq("desktop") {
        margin-bottom: 60px;
      }
    }

    .row._gap-20{
      row-gap: 20px;
      column-gap: 10px;
      @include mq ("tablet"){
        column-gap: 20px;
      }
    }
  }

  &-location {
    padding: 40px 0 40px;

    @include mq("phablet") {
      padding: 40px 0 105px;
    }
    @include mq("tablet") {
      padding: 40px 0 125px;
    }
    @include mq("tablet-wide") {
      padding: 50px 0 145px;
    }
    @include mq("desktop") {
      padding: 60px 0 165px;
    }

    .row._gap-80{
      gap: 15px;
    }

    .row {
      .col {
        &:first-child {
          display: flex;
          flex-wrap: wrap;
          align-items: flex-start;
        }
      }
    }

    &__heading {
      &--main {
        margin-bottom: 30px;
        @include mq("phablet") {
          margin-bottom: 50px;
        }
        @include mq("tablet") {
          margin-bottom: 60px;
        }
        @include mq("tablet-wide") {
          margin-bottom: 70px;
        }
        @include mq("desktop") {
          margin-bottom: 80px;
        }
      }
      &--sub {
        margin-bottom: 15px;
        @include mq("tablet") {
          margin-bottom: 25px;
        }
        @include mq("desktop") {
          margin-bottom: 35px;
        }
      }
    }

    &__details {
      margin-bottom: 13px;
      @include mq("phablet") {
        margin-bottom: 35px;
      }
      @include mq("tablet") {
        margin-bottom: 45px;
      }
      @include mq("tablet-wide") {
        margin-bottom: 55px;
      }
      @include mq("desktop") {
        margin-bottom: 65px;
      }
      &-box {
        span {
          font-size: 1.2rem;
          line-height: 1.8;
          letter-spacing: 0.05em;
          font-weight: 500;

          @include mq("tablet") {
            font-size: 1.4rem;
          }
          @include mq("tablet-wide") {
            font-size: 1.6rem;
          }
          @include mq("desktop") {
            font-size: 1.8rem;
          }
        }
        &:before {
          content: "";
          display: inline-block;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100% 100%;
        }
        &--address {
          display: flex;
          gap: 10px;
          @include mq("tablet") {
            gap: 15px;
          }
          &:before {
            width: 12px;
            height: 17px;
            margin-top: 7px;
