.cc-vehicles {
  &-box {
    background-color: $c-white;
    padding: 5.5px 5.5px 8px;
    box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    height: 100%;

    &:hover {
      .cc-vehicles-list__item-img img {
        scale: 1.1;
        transition: 0.4s all ease-in-out;
      }
      .cc-vehicles-link:after {
        transition: 0.4s all ease-in-out;
        transform: translateX(-5px);
      }
    }
  }
  &-list {
    margin-bottom: 10px;
    flex-wrap: wrap;
    padding-inline: 5px;

    @include mq("tablet") {
      margin-bottom: 25px;
      padding-inline: 0;
    }
    @include mq("desktop") {
      margin-bottom: 40px;
    }

    &__item {
      flex-basis: 100%;
      max-width: 16.7rem;
      @include mq("phablet") {
        flex-basis: calc((100% / 2) - 10px);
        max-width: calc(100% / 2);
      }
      @include mq("tablet") {
        flex-basis: calc((100% / 3) - 14px);
        max-width: calc(100% / 3);
      }

      &-img img {
        transition: 0.4s all ease-in-out;
      }
    }
  }

  &-link,
  &-link:visited,
  &-link:link,
  &-link:focus {
    align-self: flex-end;
    font-size: 1.2rem;
    letter-spacing: 0.05em;
    font-weight: 700;
    color: $c-primary;
    display: flex;
    align-items: center;
    gap: 10px;

    @include mq("tablet") {
      font-size: 1.4rem;
      gap: 15px;
    }
    @include mq("tablet-wide") {
      font-size: 1.6rem;
    }
    @include mq("desktop") {
      font-size: 1.8rem;
      gap: 20px;
    }

    &:after {
      content: "";
      background-image: url("data:image/svg+xml,%3Csvg id='Group_91' data-name='Group 91' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='23.135' height='23.179' viewBox='0 0 23.135 23.179'%3E%3Cdefs%3E%3CclipPath id='clip-path'%3E%3Crect id='Rectangle_19' data-name='Rectangle 19' width='23.135' height='23.179' fill='%23fff'/%3E%3C/clipPath%3E%3C/defs%3E%3Cg id='Group_44' data-name='Group 44' clip-path='url(%23clip-path)'%3E%3Cpath id='Path_16' data-name='Path 16' d='M10.845,0h1.446c.148.019.3.039.444.055A11.339,11.339,0,0,1,18.852,2.6a11.408,11.408,0,0,1,4.106,6.993c.076.421.119.848.178,1.272v1.449c-.035.283-.066.567-.106.849a11.408,11.408,0,0,1-2.811,6.1A11.376,11.376,0,0,1,13.582,23c-.427.077-.86.121-1.291.18H10.845c-.267-.033-.536-.06-.8-.1A11.367,11.367,0,0,1,2.5,18.759,11.261,11.261,0,0,1,.108,10.046,11.228,11.228,0,0,1,3.379,3.42,11.3,11.3,0,0,1,9.6.175C10.011.1,10.429.058,10.845,0M21.689,11.584A10.121,10.121,0,1,0,11.562,21.731,10.147,10.147,0,0,0,21.689,11.584' transform='translate(-0.001)' fill='%232B2B51'/%3E%3Cpath id='Path_17' data-name='Path 17' d='M115.391,125.048h-2.653c-1.408,0-2.815,0-4.223,0a.722.722,0,0,1-.709-1,.705.705,0,0,1,.7-.444h6.88c-.082-.09-.133-.15-.189-.205-.718-.72-1.44-1.437-2.154-2.162a.718.718,0,0,1,.3-1.226.67.67,0,0,1,.689.173q1.825,1.819,3.64,3.648a.692.692,0,0,1-.008,1q-1.806,1.822-3.625,3.632a.705.705,0,0,1-1.015,0,.716.716,0,0,1,.014-1.033c.715-.724,1.436-1.441,2.154-2.161.057-.057.111-.116.2-.213' transform='translate(-101.241 -112.734)' fill='%232B2B51'/%3E%3C/g%3E%3C/svg%3E%0A");
      width: 19px;
      height: 19px;
      display: inline-block;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 100%;
      transition: 0.4s all ease-in-out;
      @include mq("phablet") {
        width: 20px;
        height: 20px;
      }
      @include mq("tablet") {
        width: 21px;
        height: 21px;
      }
      @include mq("tablet-wide") {
        width: 22px;
        height: 22px;
      }
      @include mq("desktop") {
        width: 23px;
        height: 23px;
      }
    }
  }
  &-links {
    &__img {
      display: block;
      margin-bottom: 6px;
      border-radius: 6px;
      overflow: hidden;

      @include mq("tablet"){
        margin-bottom: 20px;
      }

      img {
        width: 100%;
      }
    }

    &__heading {
      color: $c-black;
      font-size: 1.4rem;
      line-height: 1.44;
      font-weight: 700;
      margin-bottom: 5px;

      @include mq("tablet") {
        font-size: 1.8rem;
        margin-bottom: 15px;
      }
      @include mq("tablet-wide") {
        font-size: 2.4rem;
      }
      @include mq("desktop") {
        font-size: 2.5rem;
        margin-bottom: 20px;
      }
    }
    &__cat {
      &-list {
        display: flex;
        gap: 3px;
        margin-bottom: 5px;
        flex-wrap: wrap;
        @include mq("tablet") {
          margin-bottom: 10px;
        }
        @include mq("desktop") {
          margin-bottom: 15px;
        }
      }

      &-item {
        position: relative;
        flex-basis: calc((100% / 3) - 3px);
        text-align: center;
      }

      &-link,
      &-link:link,
      &-link:visited,
      &-link:focus {
        border-radius: 4px;
        color: $c-white;
        background-color: $c-primary;
        font-size: 1rem;
        padding: 0 5px;
        line-height: 1.625;
        border: 1px solid $c-text;
        display: block;
        font-weight: 500;
        transition: 0.4s all ease-in-out;
        @include line-clamp(1);

        @include mq("tablet-wide") {
          font-size: 1.4rem;
        }
        @include mq("desktop") {
          font-size: 1.6rem;
        }
      }
    }

    &__paragraph {
      font-size: 1rem;
      line-height: 1.875;
      margin-bottom: 10px;
      flex-grow: 1;
      color: $c-black;
      @include line-clamp(3);

      @include mq("tablet-wide") {
        font-size: 1.4rem;
        margin-bottom: 20px;
      }
      @include mq("desktop") {
        font-size: 1.6rem;
        margin-bottom: 30px;
      }
    }
  }
}
